.gallaryWrapper{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 15px;
    overflow: scroll;
    padding-bottom: 30px;
}

.pics{
    z-index: 0;
    flex-basis: 40%;
    width: 45%;
    object-fit: cover;
    filter: drop-shadow(10px 10px 10px #000);
    border-radius: 10px;
    border: 1px #03240E solid;
}