  @import url(../MainPage/MainComponents/css/fontDefault.css);

  .wrapper {
    width: 100vw;
    overflow: hidden;
    background-color: crimson;
    font-family:'배민 한나체';
  }
  
  .wrapper div,
  .wrapper p {
    position: relative;
    z-index: 2;
  }
  
  .item {
    position: absolute;
    top: 1600px;
    right: 0;
    opacity: 0.5;
    z-index: 1;
  }
  
  .item_snow {
    left: 100px;
    right: auto;
    width: 200px;
    opacity: 1;
  }
  
  .bg {
    width: 100%;
    margin: 0 auto;
    display: flex;
    height: 500px;
    justify-content: center;
    align-items: center;
  }
  
  .bg div {
    color: crimson;
    display: inline-block;
    background: rgba(255, 255, 255, 0.5);
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    padding: 3rem;
  }

  .bg5 div {
    display: inline-block;
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    padding: 0 1rem 0 1rem;
    border-radius: 15px;
  }

  .bg1 {
    background: url(../../../public/이태원.jpeg) no-repeat;
    background-position: top center;
    background-size: cover;
  }
  
  .bg2 {
    margin-top: 50px;
    background: url(../../../public/하회탈.jpeg) no-repeat;
  }
  
  .bg3 {
    background: url(../../../public/나없을때.jpeg) no-repeat;
    background-position: top center;
    background-size: cover;
  }

  .bg4 {
    background: url(../../../public/보랑.jpeg) no-repeat;
    background-position: top center;
    background-size: cover;
  }

  .bg5 {
    background: url(../../../public/생인컷네.png) no-repeat;
    background-position: top center;
    background-size: cover;
  }

  .desc {
    font-weight: bold;
    font-size: 80px;
    color: tomato;
    left: 50%;
  }
  
  .desc2 {
    font-weight: bold;
    font-size: 60px;
    color: rgb(70, 70, 223);
    right: 50%;
  }
  
  .desc3 {
    font-weight: bold;
    text-align: center;
    font-size: 60px;
    color: #666;
    opacity: 1;
  }
  
  button,
button::after {
  -webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
  -o-transition: all 0.3s;
	transition: all 0.3s;
}

button {
  background: none;
  border: 3px solid #fff;
  border-radius: 5px;
  color: #fff;
  display: block;
  font-family: '배민 한나체';
  font-size: 1rem;
  font-weight: bold;
  margin: 1em auto;
  padding: 2em 6em;
  position: relative;
  text-transform: uppercase;
}

button::before,
button::after {
  background: #fff;
  content: '';
  position: absolute;
  z-index: -1;
}

button:hover {
  color: #2ecc71;
}
.btn-1::after {
    height: 0;
    left: 0;
    top: 0;
    width: 100%;
  }
  
  .btn-1:hover:after {
    height: 100%;
  }
  .btnLink {
    text-decoration: none;
  }