@import url(./fontDefault.css);

.Header{
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
    color: #d3d3d3;
    border-bottom: 0.5px solid #d3d3d3;
    position: fixed;
    z-index: 2;
}

.Header h1{
    font-family: 'X-masFontTitle2';
    margin-top: 0.5rem;
    z-index: 2;
}

.Header::after{
    width: 100%;
    height: 100%;
    content: "";
    background-color: crimson;
    position: absolute;
    z-index: 1;
    opacity: 0.85;
}